.container {
  width: 95%;
  margin: 0 auto;
  padding: 10px 0px;
}

.content {
  margin-top: 72px;
}

@media (min-width: 1000px) and (min-height: 700px) {
  .Burger {
    width: 700px;
    height: 600px;
  }
}
@media (min-width: 500px) and (min-height: 401px) {
  .Burger {
    width: 450px;
    height: 400px;
  }
}
